"use client";

import React from "react";
import Link from "next/link";

const Navbar: React.FC = () => {
  return (
    <nav className="w-full bg-[#9a9a98] bg-opacity-90 backdrop-blur-md z-50">
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
        <div className="flex justify-between h-16 items-center">
          {/* Logo */}
          <div className="text-white text-2xl font-semibold">
            @ Anghelo
          </div>

          {/* Nav Links */}
          <div className="hidden md:flex space-x-8">
            <Link href="#about" className="text-white hover:text-gray-200 transition">
              About
            </Link>
            <Link href="#projects" className="text-white hover:text-gray-200 transition">
              Projects
            </Link>
            <Link href="#contact" className="text-white hover:text-gray-200 transition">
              Contact
            </Link>
          </div>

          {/* Mobile menu placeholder (optional) */}
          {/* You can add a hamburger menu here if you want */}
        </div>
      </div>
    </nav>
  );
};

export default Navbar;
